module.exports = "\n/* <------- Navigation Bar Styling ------->*/\nimg{\n  position: fixed;\n  top:-200px;\n  filter: blur(8px);\n  -webkit-filter: blur(4px);\n  filter: brightness(75%);\n}\n.nav_bar{\n  width:100%;\n  display: flex;\n  position: fixed;\n  margin-top: 20px;\n  z-index: 1;\n}\n.nav_bar ul {\n  position: fixed;\n  right:100px;\n}\n/*------->> Search */\n.fa-search{\n  color:white;\n  font-size: 18px;\n  padding-top: 12px;\n  padding-left: 20px;\n}\n.search{\n  position: relative;\n  left:100px;\n  background-color: rgba(0, 0, 0, 0.171);\n  border-radius: 10px;\n}\n.search input{\n  width:400px;\n  border:none;\n  height:40px;\n  font-size: 16px;\n  color:white;\n  font-weight: 100;\n  padding-left: 15px;\n  background-color: rgba(255, 255, 255, 0);\n}\n.search input:focus{\n  border:none;\n  outline: none;\n}\n::-webkit-input-placeholder { /* Chrome/Opera/Safari */\n  color:rgba(255, 255, 255, 0.884);\n}\n::-moz-placeholder { /* Firefox 19+ */\n  color:rgba(255, 255, 255, 0.884);\n}\n:-ms-input-placeholder { /* IE 10+ */\n  color:rgba(255, 255, 255, 0.884);\n}\n:-moz-placeholder { /* Firefox 18- */\n  color:rgba(255, 255, 255, 0.884);\n}\n/*------->> Login/Register */\n.nav_bar ul li{\n  width:150px;\n  height:40px;\n  border:1px solid rgb(255, 255, 255);\n  text-align: center;\n  display: inline-block;\n  padding-top: 6px;\n  border-radius: 4px;\n  margin:0 10px;\n  cursor: pointer;\n  transition: .5s;\n}\n.nav_bar ul li a{\n  text-decoration: none;\n  font-size: 18px;\n  text-align: center;\n  transition: .5s;\n}\n.about{\n  background-color: white !important;\n  border:none !important;\n  padding-top: 8px !important;\n  transition: .5s;\n}\n.about a {\n  color:hsl(184, 24%, 55%) !important;\n}\n.nav_bar ul li:hover{\n  background-color: white;\n}\n.nav_bar ul li:hover a {\n  color:hsl(184, 24%, 55%);\n}\n.about:hover {\n  background-color: rgb(92, 56, 109) !important;\n}\n/* <------- Header Styling ------->*/\n.page_head{\n  position: relative;\n  top:220px;\n  width:400px;\n  margin:auto;\n}\n.head_text{\n  height:400px;\n  margin:auto;\n  transition:.5s;\n}\n.page_head h1 {\n  font-size: 80px;\n  font-family: 'Nunito', sans-serif;\n  text-align: center;\n}\n.head_text{\n  width:90%;\n  margin:auto;\n}\n.page_head h4 {\n  margin-top:150px;\n  font-size: 40px;\n  font-family: 'Nunito', sans-serif;\n  text-align: center;\n}\n.scroll_main{\n  position: relative;\n  font-size: 60px;\n  padding-right: 20px;\n  top:30px;\n  color:white !important;\n}\n.login_input{\n  display:block;\n  width:100%;\n  border:2px solid rgb(233, 233, 233);\n  border-radius: 3px;\n  height:40px;\n  margin-top: 20px;\n  margin-bottom: 15px;\n  color: rgb(233, 233, 233) !important;\n  text-align: center;\n  font-size: 18px;\n  background-color: rgba(255, 255, 255, 0);\n  transition:.5s;\n}\n.login_input:hover{\n  background-color: rgba(255, 255, 255, 0.61);\n  color: rgb(29, 29, 29) !important;\n  border:2px solid rgba(255, 255, 255, 0.61);\n  border-radius: 3px;\n}\n.login_input:hover::-webkit-input-placeholder { /* Chrome/Opera/Safari */\n  color: rgb(65, 65, 65) !important;\n  font-weight: bold;\n}\n::-webkit-input-placeholder { /* Chrome/Opera/Safari */\n  color: rgb(233, 233, 233) !important;\n  transition:.5s;\n}\n::-moz-placeholder { /* Firefox 19+ */\n  color: rgb(233, 233, 233) !important;\n  transition:.5s;\n}\n:-ms-input-placeholder { /* IE 10+ */\n  color: rgb(233, 233, 233) !important;\n  transition:.5s;\n}\n:-moz-placeholder { /* Firefox 18- */\n  color: rgb(233, 233, 233) !important;\n  transition:.5s;\n}\n.login_input:focus{\n  outline:none;\n  border:2px solid  rgba(255, 255, 255, 0.61);\n  border-radius: 3px;\n  font-weight: bold;\n}\n.submit {\n  display:block;\n  width:100%;\n  border-radius: 20px;\n  border:none;\n  height:40px;\n  margin-top: 30px;\n  margin-bottom: 15px;\n  color: rgb(233, 233, 233) !important;\n  text-align: center;\n  font-size: 18px;\n  background-color: rgba(61, 37, 71, 0.822);\n  transition:.5s;\n}\n.submit:hover{\n  background-color: rgb(92, 56, 109);\n}\n.error {\n  text-align: center;\n  background-color: rgba(0, 0, 0, 0.2);\n  border-radius: 3px;\n}\n\n\n/*# sourceMappingURL=data:application/json;base64, */"